html,body {
  min-height:100vh;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: linear-gradient(160deg, rgb(0, 147, 233) 0%, rgb(128, 208, 199) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

kbd {
  display:inline-block;
  border:3px outset #aaa;
  border-radius:0.375rem;
  font-size:1.2rem;
  padding-left:0.375rem;
  padding-right:0.375rem;
  margin-top:-3px;
  margin-bottom:-3px;
  background:#fff;
  color:#000;
}

h1 {
  font-size:1.5rem;
  line-height:1.5rem;
  margin: 0 0 1.5rem;
}

h2 {
  font-size:1.3rem;
  line-height:1.5rem;
  margin: 0 0 0.75rem;
}

ul {
  padding-left:1.5rem;
}

p, ul, li {
  margin: 0 0 0.75rem;
}

a {
  text-decoration: none;
  color: rgb(0, 147, 233);
}

.box {
  position:relative;
  background:#fff;
  border-radius:0.375rem;
  line-height:1.5rem;
  margin: 0.75rem 0.75rem 3rem;
  flex-direction:column;
}

.box>:first-child {
  margin-top:0.75rem;
}

.box>h1 {
  margin-bottom:0.75rem;
}
.box>h2 {
  margin-bottom:0.75rem;
}

.box>* {
  margin-left:0.75rem;
  margin-right:0.75rem;
}

.box>img {
  display:block;
  margin-left:0;
  margin-right:0;
}

.box>:last-child {
  margin-bottom:0.75rem;
}

.box>img:last-child {
  margin-bottom:0;
  border-bottom-left-radius: 0.375rem;
  border-bottom-right-radius: 0.375rem;
}

.box>svg {
  position:relative;
  flex: 1;
  max-height:100%;
  max-width:100%;
}

.boxcontainer {
  display: flex;
  flex-direction: row;
  max-height:70vh;
}

.boxcontainer .box {
  flex: 1;
}

.box>.text {
  padding:0.75rem;
  margin:0;
}

.text {
  display:block;
  position: relative;
  line-height:1.5rem;
  font-size:1.1rem;
}

.text :last-child {
  margin-bottom:0;
}
